import Modal from "../../components/UI/Modal/Modal"
import classes from "./GearEditor.module.css"
import React from "react"
import gearHead from "../../assets/json/parameters/GearInfoHead.json"
import gearClothes from "../../assets/json/parameters/GearInfoClothes.json"
import gearShoes from "../../assets/json/parameters/GearInfoShoes.json"
import lang from "../../assets/json/language/EUen.json"
import Input from "../../components/UI/Input/Input"
import ability_list from "../../assets/json/parameters/SkillIds.json"
import ability_list_rev from "../../assets/json/parameters/SkillIdsRev.json"
import { getName, skillImage, brandImage, gearImage } from "../../Utils"

const GearEditor = props => {
    const newType = props.newType
    const newGear = props.newGearId
    const langList =
        newType === 0
            ? lang["CommonMsg/Gear/GearName_Head"]
            : newType === 1
            ? lang["CommonMsg/Gear/GearName_Clothes"]
            : lang["CommonMsg/Gear/GearName_Shoes"]
    const gearList = newType === 0 ? gearHead : newType === 1 ? gearClothes : gearShoes
    const pgear = gearList.filter(elem => elem.Id === newGear)
    const gear = pgear.length === 0 ? null : pgear[0]

    return (
        <Modal show={props.show} modalClosed={props.onHide} wide={newGear === -1}>
            {newGear === -1 ? (
                <GearList currentGearList={props.currentGearList} onClick={props.newGearHandler} />
            ) : (
                <GearElement
                    onSave={props.onSave}
                    onDelete={props.onDelete}
                    seed={props.newSeed}
                    main={props.newMain}
                    gear={gear}
                    langList={langList}
                    seedValidator={props.seedValidator}
                    loadAbility={props.loadAbility}
                    loadAbilityHandler={props.loadAbilityHandler}
                    abilityOverwriteHandler={props.abilityOverwriteHandler}
                    type={newType}
                    history={props.history}
                    historyDrinks={props.historyDrinks}
                />
            )}
        </Modal>
    )
}

const GearList = props => {
    return (
        <div className={classes.GearList}>
            {gearHead
                .filter(elem => elem.HowToGet !== "Impossible")
                .filter(elem => props.currentGearList.HaveGearHeadMap[elem.Id] === undefined)
                .map((elem, idx) => {
                    return (
                        <div key={idx} className={classes.GearListElem} onClick={() => props.onClick(elem.Id, 0)}>
                            <img src={gearImage(elem.__RowId)} alt={elem.__RowId} />
                        </div>
                    )
                })}
            {gearClothes
                .filter(elem => elem.HowToGet !== "Impossible")
                .filter(elem => props.currentGearList.HaveGearClothesMap[elem.Id] === undefined)
                .map((elem, idx) => {
                    return (
                        <div key={idx} className={classes.GearListElem} onClick={() => props.onClick(elem.Id, 1)}>
                            <img src={gearImage(elem.__RowId)} alt={elem.__RowId} />
                        </div>
                    )
                })}
            {gearShoes
                .filter(elem => elem.HowToGet !== "Impossible")
                .filter(elem => props.currentGearList.HaveGearShoesMap[elem.Id] === undefined)
                .map((elem, idx) => {
                    return (
                        <div key={idx} className={classes.GearListElem} onClick={() => props.onClick(elem.Id, 2)}>
                            <img src={gearImage(elem.__RowId)} alt={elem.__RowId} />
                        </div>
                    )
                })}
        </div>
    )
}

const GearElement = props => {
    const name = props.gear === null ? "" : getName(props.langList, props.gear)
    const gear_image = props.gear === null ? skillImage("Unknown") : gearImage(props.gear.__RowId)
    const brand_image = props.gear === null ? null : brandImage(props.gear.Brand)
    return (
        <div className={classes.GearEditor}>
            <div className={classes.GearName}>{name}</div>
            <div className={classes.GearImage}>
                <img src={gear_image} alt={props.gear === null ? "Unknown" : props.gear.__RowID} />
            </div>
            <div className={classes.BrandImage}>
                {brand_image === null ? null : <img src={brand_image} alt={props.gear.Brand} />}
            </div>
            <div
                className={classes.MainAbility}
                onClick={() => props.loadAbilityHandler(0)}
                style={props.loadAbility === 0 ? { border: "2px solid black" } : null}
            >
                <img src={skillImage(props.main)} className={classes.abilityImage} alt={props.main} />
            </div>
            <div className={classes.Subs}>
                <table className={classes.Table}>
                    <thead>
                        <tr>
                            <th className={classes.TableHead}>Slot</th>
                            <th className={classes.TableHead}>Ability</th>
                            <th className={classes.TableHead}>Drink</th>
                        </tr>
                    </thead>
                    <tbody>
                        {props.history.map((elem, idx) => {
                            return (
                                <tr key={idx} className={idx & (1 == 0) ? classes.TDEven : classes.TDOdd}>
                                    <td>{idx}</td>
                                    <td
                                        className={classes.TableAbility}
                                        onClick={() => props.loadAbilityHandler(2 * idx + 1)}
                                        style={props.loadAbility === 2 * idx + 1 ? { border: "2px solid black" } : null}
                                    >
                                        <img
                                            src={skillImage(
                                                ability_list_rev[elem] ? ability_list_rev[elem] : "Unknown"
                                            )}
                                            alt={ability_list_rev[elem] ? ability_list_rev[elem] : "Unknown"}
                                            className={classes.abilityImage}
                                        />
                                    </td>
                                    <td
                                        className={classes.TableAbility}
                                        onClick={() => props.loadAbilityHandler(2 * idx + 2)}
                                        style={props.loadAbility === 2 * idx + 2 ? { border: "2px solid black" } : null}
                                    >
                                        <img
                                            src={skillImage(
                                                ability_list_rev[
                                                    props.historyDrinks[idx] ? props.historyDrinks[idx] : 255
                                                ]
                                            )}
                                            alt={
                                                ability_list_rev[
                                                    props.historyDrinks[idx] ? props.historyDrinks[idx] : 255
                                                ]
                                            }
                                            className={classes.abilityImage}
                                        />
                                    </td>
                                </tr>
                            )
                        })}
                        <tr className={props.history.length & (1 == 0) ? classes.TDEven : classes.TDOdd}>
                            <td>{props.history.length}</td>
                            <td
                                className={classes.TableAbility}
                                onClick={() => props.loadAbilityHandler(2 * props.history.length + 1)}
                                style={
                                    props.loadAbility === 2 * props.history.length + 1
                                        ? { border: "2px solid black" }
                                        : null
                                }
                            >
                                <img src={skillImage("Unknown")} alt={"Unknown"} className={classes.abilityImage} />
                            </td>
                            <td
                                className={classes.TableAbility}
                                onClick={() => props.loadAbilityHandler(2 * props.history.length + 2)}
                                style={
                                    props.loadAbility === 2 * props.history.length + 2
                                        ? { border: "1px solid black" }
                                        : null
                                }
                            >
                                <img src={skillImage("Unknown")} alt={"Unknown"} className={classes.abilityImage} />
                            </td>
                        </tr>
                    </tbody>
                </table>
            </div>
            <div className={classes.Seed}>
                <Input title="Seed" changed={props.seedValidator} val={`0x${(props.seed >>> 0).toString(16)}`} />
            </div>
            <div className={classes.Control}>
                {props.loadAbility !== -1 ? (
                    <div className={classes.extraZone}>
                        <div className={classes.abilityHeader}>Abilities</div>
                        <div className={classes.abilitiyList}>
                            {Object.keys(ability_list)
                                .filter(elem => {
                                    return (
                                        ability_list[elem] < 100 ||
                                        ability_list[elem] === 255 ||
                                        (props.loadAbility === 0 &&
                                            props.type === 0 &&
                                            ability_list[elem] >= 100 &&
                                            ability_list[elem] <= 103) ||
                                        (props.loadAbility === 0 &&
                                            props.type === 1 &&
                                            ability_list[elem] >= 104 &&
                                            ability_list[elem] <= 108) ||
                                        (props.loadAbility === 0 &&
                                            props.type === 2 &&
                                            ability_list[elem] >= 109 &&
                                            ability_list[elem] <= 111)
                                    )
                                })
                                .map((ability, idx) => {
                                    return (
                                        <div
                                            key={idx}
                                            onClick={() =>
                                                props.abilityOverwriteHandler(props.loadAbility, ability_list[ability])
                                            }
                                        >
                                            <img
                                                src={skillImage(ability)}
                                                className={classes.abilityImage}
                                                alt={ability}
                                            />
                                        </div>
                                    )
                                })}
                        </div>
                    </div>
                ) : null}
            </div>
            <div className={classes.Save}>
                <button className={classes.Button} onClick={props.onSave}>
                    Save
                </button>
                <button className={classes.ButtonDelete} onClick={props.onDelete}>
                    Delete
                </button>
            </div>
        </div>
    )
}

export default GearEditor
